function headerPluginFuncDefault () {
  return {}// like {'someKey': 'someValue', 'someKey2': 'someValue2'}
}
class Request {
  constructor (server, baseUrl, headerPluginFunc, resultCodeHandlers) {
    if (!headerPluginFunc) {
      headerPluginFunc = headerPluginFuncDefault
    }

    this._server = server
    this._baseUrl = baseUrl
    this._uri = ''
    this._params = null
    this._data = null
    this._successFunc = function (result) {
      console.log('Request.success:' + result)
    }
    this._errorFunc = function (errorModule, errorCode, errorMessage) {
      console.log('Request.error:' + errorModule + '_' + errorCode + ',' + errorMessage)
    }
    this._transformResponseFunc = null
    this._headerPluginFunc = headerPluginFunc
    this._resultCodeHandlers = resultCodeHandlers
  }

  uri (uri) {
    this._uri = uri
    return this
  }
  url (uri) {
    if (uri) {
      this.uri(uri)
    }
    return this._baseUrl + this._uri
  }
  transformResponse (transformResponse) {
    this._transformResponse = transformResponse
    return this
  }
  headers (headers) {
    this._headers = headers
    return this
  }
  params (params) {
    this._params = params
    return this
  }
  data (data) {
    this._data = data
    return this
  }
  success (successFunc) {
    this._successFunc = successFunc
    return this
  }
  error (errorFunc) {
    this._errorFunc = errorFunc
    return this
  }
  post () {
    this._request('post')
  }
  get () {
    this._request('get')
  }
  _request (method) {
    const __successFunc = this._successFunc
    const __errorFunc = this._errorFunc
    var __headers = this._headers
    if (this._headerPluginFunc) {
      __headers = Object.assign(__headers || {}, this._headerPluginFunc())
    }
    if (__headers) {
      for (let h in __headers) {
        __headers[h] = encodeURI(__headers[h])
      }
    }
    var s
    if (this._transformResponse) {
      s = this._server.request({
        method: method,
        url: this._uri,
        params: this._params,
        data: this._data,
        headers: __headers,
        transformResponse: this._transformResponse
      })
    } else {
      s = this._server.request({
        method: method,
        url: this._uri,
        params: this._params,
        data: this._data,
        headers: __headers
      })
    }

    var thisResultCodeHandlers = this._resultCodeHandlers
    function _resultCodeHandle (errorModule, errorCode, result) {
      if (thisResultCodeHandlers) {
        let handlerFunc = thisResultCodeHandlers[errorModule + '_' + errorCode]
        if (!handlerFunc) {
          handlerFunc = thisResultCodeHandlers[errorCode]
        }
        if (handlerFunc) {
          if (!handlerFunc(errorModule, errorCode, result)) {
            return
          }
        }
      }
      if (errorCode === 0) {
        __successFunc(result)
      } else {
        __errorFunc(errorModule, errorCode, result)
      }
    }

    s.then(function (response) {
      if (response.status === 200) {
        if (response.data.code === 0) {
          _resultCodeHandle(null, 0, response.data.data)
        } else {
          _resultCodeHandle(response.data.module, response.data.code, response.data.message)
        }
      } else {
        _resultCodeHandle('browser_net', -1, response.status + ',' + response.statusText)
      }
    })
      .catch(function (err) {
        _resultCodeHandle('browser_local', -2, err)
      })
  }
}

class MockServer {
  constructor (serverName, baseURL, mockObj) {
    this.mockObj = mockObj
    this.serverName = serverName
    this.baseURL = baseURL
  }

  // method, uri, data, headers
  request (options) {
    if (!options) {
      options = Server.RequestOptionExample()
    }

    var mockRes = this._request(options.method, options.url, options.data, options.headers)
    if (mockRes.status === 200 && mockRes.data.code === 0 && mockRes.data.data === undefined && options.url.indexOf('/someurl') < 0) {
      alert(options.url + '\n请开发人员添加演示版Mock数据！！！')
    }
    return {
      then: function (func) {
        if (!mockRes.error) {
          func(mockRes)
        }
        return this
      },
      catch: function (func) {
        if (mockRes.error) {
          func(mockRes.error)
        }
        return this
      }
    }
  }
  _request (method, uri, data, headers) {
    console.log('------------------ mock post ------------------ [')
    console.log(uri)
    console.log(data)
    console.log(headers)

    let result = this.mockObj[uri]

    if (typeof this.mockObj.request === 'function') {
      result = this.mockObj.request(method, uri, data, headers)
    } else {
      result = this.mockObj[uri]
    }

    if (typeof result === 'function') {
      result = result(data, headers)
    }

    console.log('----------------- mock result ----------------- ]  ')
    console.log(result)

    if (result) {
      if (result.isHttpResponse) {
        // 模拟异常信息时，需要有result.error字段，为字符串
        return result
      } else if (result.isApiResponse) {
        return {
          isHttpResponse: true,
          // status 为-1模拟未能成功发送
          status: 200,
          // 从服务器返回的http状态文本
          statusText: 'OK',
          // 响应头信息
          headers: {},
          // `config`是在请求的时候的一些配置信息
          config: {},
          // 模拟异常信息时，需要有error字段
          // error: 'some error',
          // 数据
          data: result
        }
      }
    }

    return {
      isHttpResponse: true,
      // status 为-1模拟未能成功发送
      status: 200,
      // 从服务器返回的http状态文本
      statusText: 'OK',
      // 响应头信息
      headers: {},
      // `config`是在请求的时候的一些配置信息
      config: {},
      // 模拟异常信息时，需要有error字段
      // error: 'some error',
      // 数据
      data: {
        isApiResponse: true,
        // 错误发生模块
        module: '',
        // 错误代码
        code: 0,
        // 错误信息
        message: '',
        data: result
      }
    }
  }
}

Axios.defaults.headers.post['Content-Type'] = 'application/json;charset=utf-8'
class Servers {
  constructor (serverBaseConfig, mockModules, options) {
    this.serverMap = {}
    this.serverBaseConfig = serverBaseConfig
    this.mockModules = mockModules
    if (options) {
      this._options = Object.assign({}, options)
    } else {
      this._options = Servers.OptionsExample()
    }
  }

  init (serverBaseConfig, mockModules, options) {
    this.serverBaseConfig = serverBaseConfig
    this.mockModules = mockModules
    if (options) {
      this._options = Object.assign({}, options)
    }
  }

  // 首字母大写的方法，代替静态常量
  static OptionsExample () {
    return {
      getHttpHeaders: function () {
        return {}
      },
      errorHandlers: {
        // key的格式：errorModule_errorCode
        'moduleabc_codexyz': function (errorModule, errorCode, errorMsg) {
          return true// true 表示，继续后面的处理，false表示终止后面的处理
        }
      }
    }
  }
  options (options) {
    this._options = Object.assign(this._options, options)
  }

  createSelfAxiosServer () {
    return Axios.create()
  }

  createAxiosServer (serverName) {
    if (this._options.timeout === undefined) {
      this._options.timeout = 10000
    }
    return Axios.create({
      baseURL: this.serverBaseConfig[serverName],
      timeout: this._options.timeout
    })
  }

  createMockServer (serverName) {
    return new MockServer(serverName, this.serverBaseConfig[serverName], this.mockModules[serverName])
  }

  server (serverName) {
    if (!serverName) {
      serverName = Servers.Main()
    }
    let server = this.serverMap[serverName]
    if (server) {
      return server
    }
    if (serverName === Servers.Self()) {
      server = this.createSelfAxiosServer()
    } else if (this.mockModules) {
      server = this.createMockServer(serverName)
    } else {
      server = this.createAxiosServer(serverName)
    }
    this.serverMap[serverName] = server
    return server
  }

  request (serverName) {
    if (!serverName) {
      serverName = Servers.Main()
    }
    let baseURL = this.serverBaseConfig[serverName]
    if (!baseURL) {
      baseURL = ''
    }
    return new Request(this.server(serverName), baseURL, this._options.getHttpHeaders, this._options.errorHandlers)
  }

  // 首字母大写的方法，代替静态常量
  static Main () {
    return 'main'
  }
  // 首字母大写的方法，代替静态常量
  static Self () {
    return '~'
  }
}

export default {
  Request: Request,
  MockServer: MockServer,
  Servers: Servers
}



