'use strict'

const defaultParentFrameOption = {
  onMessageFunc: function (iframeEle, data) {
  },
  onSonCreatedFunc: function (iframeEle, pageUrl) {
  },
  onSonFlowDoneFunc: function (iframeEle, pageUrl, data) {
  },
  onSonErrorFunc: function (iframeEle, pageUrl, errorModule, errorCode, errorMsg) {
  },
  sonGoUrlFunc: function (iframeEle, toUrl, fromUrl) {
  }
}
class ParentFrame {
  constructor (options) {
    this._options = {}
    if (options !== false) {
      this.init(options)
    }
  }

  init (options) {
    if (!options) {
      options = defaultParentFrameOption
    }

    this._options = Object.assign(this._options, options)
    var thisOption = this._options

    function parentFrameOnmessage (e) {
      if (e.data && typeof e.data === 'string' && e.data.indexOf('iframemsg') === 0) {
        var json = e.data.substr(9)
        // console.log(json)
        // debugger
        var iframe = document.getElementById(e.source.name)
        json = JSON.parse(json)
        // console.log(json)
        if (json.iframeHeight) {
          iframe.style.height = json.iframeHeight + 'px'
        }
        if (json.onCreated) {
          if (thisOption.onSonCreatedFunc) {
            thisOption.onSonCreatedFunc(iframe, json.pageUrl)
          }
        }
        if (json.flowDone) {
          if (thisOption.onSonFlowDoneFunc) {
            thisOption.onSonFlowDoneFunc(iframe, json.pageUrl, json.data)
          }
        }
        if (json.goUrl) {
          thisOption.sonGoUrlFunc(iframe, json.url, json.target, json.pageUrl)
        }
        if (thisOption.onMessageFunc) {
          thisOption.onMessageFunc(iframe, json)
        }
        if (json.errorCode > 0) {
          if (thisOption.onSonErrorFunc) {
            thisOption.onSonErrorFunc(iframe, json.pageUrl, json.errorModule, json.errorCode, json.errorMsg)
          }
        }
      }
    }

    // 监听postMessage消息事件
    if (typeof window.addEventListener !== 'undefined') {
      window.addEventListener('message', parentFrameOnmessage, false)
    } else if (typeof window.attachEvent !== 'undefined') {
      window.attachEvent('onmessage', parentFrameOnmessage)
    }
  }

  option (options) {
    if (!options) {
      return
    }
    this._options = Object.assign(this._options, options)
  }

  send (iframe, jsonData) {
    if (typeof iframe === 'string') {
      iframe = document.getElementById(iframe)
    }
    iframe.contentWindow.postMessage('parentframemsg' + JSON.stringify(jsonData), '*')
  }

  sendGoUrl (iframe, url) {
    this.send(iframe, {
      'goUrl': true,
      'url': url
    })
  }

  sendLoginCtx (iframe, env, loginCtx, pageConfigs, data) {
    this.send(iframe, {'reload': true, 'env': env, 'loginCtx': loginCtx, 'pageConfigs': pageConfigs, 'data': data})
  }
}

const defaultSonFrameOption = {
  componentName: '',
  syncParentEnvFunc: function (parentEnv) {
  },
  loginCtxNotifyFunc: function (loginCtx, pageConfigs) {
  },
  onMessageFunc: function (data) {
  },
  reloadFunc: function (data) {
  }
}
class SonFrame {
  constructor (options) {
    this._options = {}
    if (options !== false) {
      this.init(options)
    }
  }

  init (options) {
    if (!options) {
      options = defaultSonFrameOption
    }

    this._options = Object.assign(this._options, options)
    var thisOption = this._options

    function sonFrameOnmessage (e) {
      // debugger
      if (e.data && typeof e.data === 'string' && e.data.indexOf('parentframemsg') === 0) {
        var json = e.data.substr(14)
        // console.log(json)
        json = JSON.parse(json)
        // console.log(json)
        if (json.env) {
          thisOption.syncParentEnvFunc(json.env)
        }
        if (json.reload) {
          thisOption.loginCtxNotifyFunc(json.loginCtx, json.pageConfigs)

          if (thisOption.reloadFunc) {
            if (!json.data) {
              json.data = {}
            }
            thisOption.reloadFunc(json.data)
          }
        }
        if (json.goUrl) {
          if (json.url === -1) {
            history.go(-1)
          } else {
            self.location.href = json.url
          }
        }
        if (thisOption.onMessageFunc) {
          thisOption.onMessageFunc(json)
        }
      }
    }

    // 监听postMessage消息事件
    if (typeof window.addEventListener !== 'undefined') {
      window.addEventListener('message', sonFrameOnmessage, false)
    } else if (typeof window.attachEvent !== 'undefined') {
      window.attachEvent('onmessage', sonFrameOnmessage)
    }

    this.sendOnCreated()
  }

  option (options) {
    if (!options) {
      return
    }
    this._options = Object.assign(this._options, options)
  }

  send (jsonData) {
    jsonData.pageUrl = self.location.href
    jsonData.sonComponent = this._options.componentName
    window.parent.postMessage('iframemsg' + JSON.stringify(jsonData), '*')
  }

  sendOnCreated () {
    this.send({
      'onCreated': true,
      'iframeHeight': document.body.scrollHeight
    })
  }

  sendIframeHeight () {
    this.send({
      'iframeHeight': document.body.scrollHeight
    })
  }

  sendFlowDone (data) {
    this.send({
      'flowDone': true,
      'data': data
    })
  }

  sendGoUrl (url, target) {
    this.send({
      'goUrl': true,
      'url': url,
      'target': target
    })
  }

  sendError (errorModule, errorCode, errorMsg) {
    this.send({
      'errorModule': errorModule,
      'errorCode': errorCode,
      'errorMsg': errorMsg
    })
  }
}

const Parent = new ParentFrame(false)
const Son = new SonFrame(false)

const SonFunc = {
  sendErrorToParent: function (errorModule, errorCode, errorMsg) {
    // 必须包一层，直接把Frameset.Son.sendError赋值给函数指针时，sendError内部的this不正确
    Son.sendError(errorModule, errorCode, errorMsg)
  }
}

export default {
  ParentFrame, SonFrame, Parent, Son, SonFunc
}



